/* retire-lah-client/src/index.css */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  @apply bg-brand-light-bg text-brand-dark-text font-sans antialiased tracking-tight;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Custom scrollbar for a cleaner look (optional) */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: #f9fafb; /* brand-light-bg slightly lighter */
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  background: #d1d5db; /* gray-300 */
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: #9ca3af; /* gray-400 */
}

/* Basic form styling if not relying entirely on @tailwindcss/forms */
input[type="number"],
select {
  @apply block w-full rounded-md border-gray-300 shadow-sm focus:border-brand-primary focus:ring focus:ring-brand-primary focus:ring-opacity-50 text-sm;
}
/* For better select arrow styling with @tailwindcss/forms */
select {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%236B7280' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='M6 8l4 4 4-4'/%3e%3c/svg%3e");
  background-position: right 0.5rem center;
  background-repeat: no-repeat;
  background-size: 1.5em 1.5em;
  padding-right: 2.5rem;
  -webkit-print-color-adjust: exact;
          print-color-adjust: exact;
}

/* Basic Tooltip Styles - Group Hover Version */
.tooltip-trigger { 
  /* The 'group' class will be added directly in the JSX/HTML */
  @apply relative inline-block cursor-help; 
}

.tooltip-content { 
  /* Handles visibility based on parent 'group' hover */
  @apply absolute z-20 w-max max-w-xs px-3 py-2 text-xs font-medium text-white bg-gray-800 rounded-lg shadow-lg 
         opacity-0 invisible group-hover:opacity-100 group-hover:visible 
         transition-opacity duration-200 ease-in-out;
  /* Positioning - above the element, centered */
  bottom: 130%; 
  left: 50%;
  transform: translateX(-50%);
  pointer-events: none; /* So tooltip doesn't interfere with hover on the trigger element */
}

/* Optional: Tooltip arrow for the .tooltip-content class */
.tooltip-content::after {
  content: "";
  position: absolute;
  top: 100%; /* At the bottom of the tooltip, pointing down */
  left: 50%;
  margin-left: -5px; /* Center the arrow */
  border-width: 5px;
  border-style: solid;
  border-color: #1f2937 transparent transparent transparent; /* Arrow color matches bg-gray-800 */
}